body {
  font: 400 16px/24px 'PT Sans', Arial, sans-serif;
  color: #000;
  margin: 0;
  min-height: 100vh;
}

ul {
  list-style: none;
  padding: 0;
}

a {
  text-decoration: none;
  transition: all .3s;
}

a:hover {
  text-decoration: underline;
}

figure {
  margin: 0;
}

.center {
  text-align: center;
}

.hidden {
  display: none;
}

#header {
  color: #fff;
  background: url("img/header-bg.jpg") top center/cover;
  padding: 24px 0 90px;
  text-align: center;
}

#header a {
  color: currentColor;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 100px;
}

.header-title {
  font: 300 48px/48px 'Open Sans', Arial, sans-serif;
  letter-spacing: 3.8px;
  text-transform: uppercase;
  margin-bottom: 75px;
}

.header-p {
  font-size: 18px;
  line-height: 30px;
}

.logo {
  color: #8dc63f;
  font-size: 23px;
  font-family: Arial, sans-serif;
  letter-spacing: -.25px;
  text-transform: uppercase;
  white-space: nowrap;
}

.logo .l-img {
  margin-right: 6px;
  vertical-align: middle;
}

.logo .l-bold-name {
  color: #287ec2;
  font-weight: bold;
}

.header-menu {
  margin: 0 -20px;
}

.header-menu > li {
  display: inline-block;
  padding: 0 20px;
}

.header-menu .hm-dropdown {
  position: relative;
  white-space: nowrap;
}

.header-menu .hm-dropdown:hover > .hm-submenu {
  opacity: 1;
  visibility: visible;
}

.header-menu .hm-submenu {
  position: absolute;
  padding: 10px 20px 15px;
  background: #19263f;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
}

.header-menu .hm-submenu > li {
  border-bottom: 1px solid #3a4761;
}

.header-menu .hm-submenu > li > a {
  display: block;
  padding: 5px 0;
}

.angle-down {
  padding-right: 20px;
  background: url("img/angle-down.svg") no-repeat center right/18px;
}

.header-auth {
  margin: 0 -10px;
}

.header-auth > li {
  display: inline-block;
  padding: 0 10px;
}

.butter {
  display: none;
  position: relative;
  cursor: pointer;
  width: 40px;
  height: 30px;
  background: #ddd;
  padding: 0;
  border: 0;
  background: transparent;
}

.butter.open > span:nth-child(1) {
  transform: translate(0, 8px) rotate(45deg);
}

.butter.open > span:nth-child(2) {
  opacity: 0;
}

.butter.open > span:nth-child(3) {
  transform: translate(0, -8px) rotate(-45deg);
}

.butter > span {
  position: absolute;
  left: 4px;
  top: 5px;
  width: 30px;
  height: 4px;
  border-radius: 2px;
  background: #0b172d;
  opacity: 1;
  transition: all .3s;
}

.butter > span:nth-child(2) {
  top: 13px;
}

.butter > span:nth-child(3) {
  top: 21px;
}

@media (max-width: 991px) {
  .header-menu,
  .header-auth {
    display: none;
  }
  .butter {
    display: block;
  }
}

@media (max-width: 575px) {
  .logo {
    font-size: 20px;
  }
}

.section {
  padding: 52px 0 10px;
}

.section-title {
  color: #19263f;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: -.25px;
  margin-bottom: 40px;
  text-align: center;
}

.section-p {
  color: #19263f;
}

.news-row {
  margin: 80px 0 0;
}

.product-row {
  margin: 62px 0 30px;
}

.news-item {
  background: #fafafa;
  padding: 28px 25px;
  min-height: 360px;
  margin-bottom: 20px;
}

.news-item .ni-link {
  color: #1657ab;
  font-size: 15px;
}

.product-item {
  margin: 20px 0;
  min-height: 200px;
}

.item-img-container {
  height: 64px;
  line-height: 64px;
  text-align: center;
}

.item-img-container > img {
  max-height: 100%;
  width: auto;
  vertical-align: middle;
}

.item-title {
  color: #19263f;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: -.25px;
  margin: 25px 0 30px;
  text-align: center;
}

.item-p {
  color: #333;
  font-size: 14px;
  line-height: normal;
  margin-bottom: 40px;
}

.item-p > a {
  color: currentColor;
  text-decoration: underline;
}

.item-p > a:hover {
  text-decoration: none;
}

.product-btn {
  display: block;
  margin: 0 auto;
  width: 215px;
  color: #19263f;
  font-size: 15px;
  text-align: center;
  padding: 12px;
  border-radius: 3px;
  border: 1px solid #80b450;
}

.companies {
  text-align: center;
  margin: 30px 0 220px;
}

.companies > img {
  margin: 10px;
}

#footer {
  color: #fff;
  background: #19263f;
  letter-spacing: -.25px;
  padding: 30px 0 50px;
}

#footer a {
  color: currentColor;
}

.footer-menu {
  font-size: 14px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 30px;
}

.footer-menu > li {
  display: inline-block;
  padding: 0 23px;
}

.footer-socials {
  text-align: center;
  margin-bottom: 30px;
}

.footer-socials > li {
  display: inline-block;
  padding: 0 15px;
}

.social-link {
  display: inline-block;
  width: 54px;
  height: 54px;
  background: no-repeat center/contain;
  text-indent: 110%;
  white-space: nowrap;
  overflow: hidden;
}

.social-link:hover {
  opacity: .7;
}

.social-link.fb {
  background-image: url("img/ico-fb.png");
}

.social-link.tw {
  background-image: url("img/ico-tw.png");
}

.social-link.in {
  background-image: url("img/ico-in.png");
}

.footer-copyright {
  text-align: center;
  font-size: 12px;
}

@media (max-width: 575px) {
  .footer-menu > li {
    padding: 0 15px;
  }
}

.drawer {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  visibility: hidden;
}

.drawer.open {
  z-index: 10;
  visibility: visible;
}

.drawer.open .d-bg {
  opacity: .8;
}

.drawer.open .d-content {
  transform: translateX(0);
}

.drawer .d-bg {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #000;
  opacity: 0;
  z-index: -1;
  transition: all .3s;
}

.drawer .d-content {
  will-change: transform;
  background: #19263f;
  height: 100%;
  width: 80%;
  transform: translateX(-100%);
  transition: all .3s;
}

.drawer-menu {
  margin: 0 0 20px;
  border-top: 1px solid #3a4761;
}

.drawer-menu li {
  display: block;
}

.drawer-menu li > a {
  display: block;
  padding: 20px;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid #3a4761;
  background-color: #23304a;
}

.drawer-menu li > a:hover {
  background-color: #2c3a54;
}

.drawer-menu li > ul > li > a {
  padding-left: 40px;
}
